.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .header {
    background-color: #ffffff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    height: 90px;
    text-align: center;
    line-height: 8;
    letter-spacing: -0.05cap;
  }
  
  .logo {
    font-size: 24px;
  }
  
  .nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .menu {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex; /* Align items horizontally */
  }
  
  .menu li {
    margin-right: 20px;
    display: inline-block; /* Display menu items inline */
  margin: 0 10px
  }

  .menu li:last-child {
    margin-right: 120px; /* Remove margin from the last item */
  }  
  
  .menu li a {
    text-decoration: none;
    color: black;
    letter-spacing: 0.5px; /* Reduce letter spacing */
    line-height: 1.5; /* Increase line height */
  }

  .menu a {
    text-decoration: none;
    color: #000000;
    font-size: 22px;
    font-family: "NotoSansTHMedium";
    letter-spacing: -0.05cap;
  }
  
  /* Mobile Menu Styles */
  .menu-icon {
    display: none;
    flex-direction: column;
    cursor: pointer;
  }
  
  .menu-icon-line {
    width: 25px;
    height: 3px;
    background-color: #000000;
    margin: 3px 0;
  }
  
  @media (max-width: 848px) {
    .nav {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 70px;
      left: 0;
      background-color: #ffffff;
      width: 100%;
      text-align: center;
      z-index: 1000;
      transition: all 0.3s ease-in;
    }
  
    .nav.open {
      display: flex;
    }
  
    .menu-icon {
      display: flex;
    }

    .menu{
      flex-direction: column; /* Align items vertically on mobile */
    }

    .menu li {
      margin-right: 0;
      margin-bottom: -50px; /* Add margin bottom to create space between items */
    }

    .menu li:last-child {
      margin-bottom: 10px;
      margin-left: 0;
      margin-right: 0
    }  
  }
  
  .white-line {
    width: 100%; /* Adjust width as needed */
    height: 35px; /* Adjust height as needed */
    background-color: white; /* Change color if necessary */
    /* Additional styling properties */
  }
  .fixed-line-a {
    position: absolute;
    top: 85px; /* Adjust as needed */
    left: 0;
    width: 100%;
    height: 10px; /* Adjust line thickness as needed */
      background: linear-gradient(
        90deg,
        #f27c7c 17%,
        #ffc480 34%,
        #fff480 50%,
        #60bf7c 66%,
        #7d8fc7 84%,
        #b67ec2 100%
      );
    z-index: 1; /* Ensure the line appears behind other content */
  }
  .fixed-line-b {
    position: absolute;
    top: 102px; /* Adjust as needed */
    left: 0;
    width: 100%;
    height: 10px; /* Adjust line thickness as needed */
      background: linear-gradient(
        90deg,
        #f27c7c 17%,
        #ffc480 34%,
        #fff480 50%,
        #60bf7c 66%,
        #7d8fc7 84%,
        #b67ec2 100%
      );
    z-index: 1; /* Ensure the line appears behind other content */
  }