.whitelinef{
  background-color: white;
  height: 100px;
  width: 100%;
  position: relative;
  transform: translateY(320%);
  display:flex;
}
.footer{
  height: 400px;
  width: 100%;
  position: relative;
  transform: translateY(80%);
  display:flex;
  flex-direction: column;
  background: linear-gradient(
      90deg,
      #f27c7c 17%,
      #ffc480 34%,
      #fff480 50%,
      #60bf7c 66%,
      #7d8fc7 84%,
      #b67ec2 100%
    );
    z-index: 10;
}

.footerleft{
    margin-top: 30px;
    margin-left:-27%;
    z-index: 100; 
}
.leftft{
  font-family: "NotoSansTHBold";
  font-size: 1.4em;
  color: #000000;
}
.footermiddle{
  position: absolute;
    margin-top: 30px;
    left:5%;
    z-index: 99; 
}
.footerright{
  position: absolute;
    margin-top: 30px;
    left:32%;
    z-index: 1; 
}
.fixed-line-blacka {
  position: absolute;
  bottom: 25px;
  left: 0;
  width: 100%;
  height: 10px;
    background: #000000;
  z-index: 1; 
}
.fixed-line-blackb {
  position: absolute;
  bottom: 45px;
  left: 0;
  width: 100%;
  height: 10px;
    background: #000000;
  z-index: 1; 
}
.a{
  text-decoration: none;
}
.mailsm {
  font-family: "NotoSansTHMedium";
  font-size: 1em;
  color: #000000;
  position: relative;
  top: 0px;
  left: 0px
}

.ppt{
  font-family: "NotoSansTHMedium";
  font-size: 1em;
  color: #000000;
}

.att{
  text-decoration: none;
  color: #000000;
  font-family: "NotoSansTHCondensedMedium";
  font-size: 1.3em;
}

.sptt{
  text-decoration: none;
  color: #000000;
  font-family: "NotoSansTHCondensedMedium";
  font-size: 1.3em;
}

.btnemail {
  display: inline-block;
  padding: 15px 63px;
  width: 24em;
  border: 0;
  border-radius: 50px;
  text-align: center;
  font-family: "NotoSansTHBold";
  text-align: center;
  background: #f27c7c;
  background: #DDDDDD
}

.footerleft,
.footermiddle,
.footerright {
  width: 100%;
}

.svgemail{
  transform: translateX(-50px);
}

@media only screen and (max-width: 808px) {
  .footerleft,
  .footermiddle,
  .footerright {
    display: none;
  }
  .footermobile {display:block; margin-top: 30px;
    margin-left:-22%}
    .footermobilet {display:block; position: absolute;
      margin-top: 30px;
      left:65%}
}
@media only screen and (min-width: 809px) {
  .footermobile {display:none; }
  .footermobilet {display:none; }
}
